<template>
    <div class="adt-button">
        <slot></slot>
        <Button v-if="allDelShow" @click="delClick">
           批量删除
           <Icon type="ios-close-circle-outline" />
        </Button>
        <adt-reconfirm-modal :showModal="showModal" @on-visible-change="visibleChange" @on-ok="allDelClick"></adt-reconfirm-modal>
    </div>
</template>

<script>
export default {
    name: 'AdtButton',
    props: {
        allDelShow: {
            type: Boolean,
            default: false
        },
        selectList: {
            type: Object
        },
        delUrl: {
            type: Function
        }
    },
    data () {
        return {
            showModal: false
        }
    },
    watch: {
    },
    methods: {
        delClick () {
            const values = Object.values(this.selectList)
            if (!values[0] || (values[0] && values[0].length <= 0)) {
                this.$Message.warning('请选择要删除的数据');
                return
            }
            this.showModal = true
        },
        allDelClick () {
            this.delUrl(this.selectList).then(res => {
                if (res.data) {
                    this.$Message.success('操作成功')
                } else {
                    this.$Message.error('操作失败')
                }
            }).catch(() => {
                this.$Message.error('操作失败')
            })
        },
        visibleChange (changeStatus) {
            this.showModal = changeStatus
        }
    }
}
</script>

<style lang="scss" scoped>
    .adt-button{
        display: inline-block;
        width: 50%;
    }
    /deep/.ivu-modal-header{
        border: none;
    }
    .ivu-btn /deep/.ivu-icon{
        margin-left: 4px;
        font-size: 16px;
    }
    /deep/.ivu-btn{
        margin-left: 30px;
    }
</style>
